import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

const RecommendationContainer = styled.div`
  width: 320px;
	margin-left: 24px;
	margin-top: 40px;
	box-sizing : border-box;
`;

const RecommendationTrending = styled.div`
	width: 100%;
  border-radius: 8px;
  background-color: #f6f8fa;
  display: flex;
  flex-direction: column;
	
	.title{
		font-family: "Montserrat","Noto Sans KR","Noto Sans JP","Noto Sans SC",sans-serif;
    font-size: 14px;
    font-weight: 700;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: -.35px;
    color: #969aa2;
    padding: 16px 0 0 20px;
	}

	.border{
		width: 280px;
    border-bottom: 1px solid #dde1e7;
    margin: 8px 0;
    align-self: center;
	}
	.contents{
		padding-inline-start: 0;
		margin: 0;
		padding: 3px 12px 12px;
    display: flex;
    flex-wrap: wrap;
		a{
			padding: 4px 8px;
    	border-radius: 8px;
    	font-family: "Montserrat","Noto Sans KR","Noto Sans JP","Noto Sans SC",sans-serif;
    	font-size: 14px;
    	font-weight: 500;
    	font-stretch: normal;
    	font-style: normal;
    	line-height: normal;
    	letter-spacing: -.35px;
    	color: #555;
    	margin-bottom: 8px;
			text-decoration: none;
			&:hover{
				background-color: #eef1f4;
				color: #00bdb8;
			}
		}
	}
`;

const keywords = [
	{
		id : 1,
		text : 'free',
		to : '/home/tags/free'
	},
	{
		id : 2,
		text : 'upload',
		to : '/home/tags/upload'
	},
	{
		id : 3,
		text : 'sharing',
		to : '/home/tags/sharing'
	},
	{
		id : 4,
		text : 'video',
		to : '/home/tags/video'
	},
	{
		id : 5,
		text : 'news',
		to : '/home/tags/news'
	},
	{
		id : 6,
		text : 'nlm',
		to : '/home/tags/nlm'
	},
	{
		id : 7,
		text : 'health',
		to : '/home/tags/health'
	},
	{
		id : 8,
		text : 'ncbi',
		to : '/home/tags/ncbi'
	},
	{
		id : 9,
		text : 'medline',
		to : '/home/tags/medline'
	},
	{
		id : 10,
		text : 'education',
		to : '/home/tags/education'
	},
	{
		id : 11,
		text : 'pubmed abstract',
		to : '/home/tags/pubmed abstract'
	},
	{
		id : 12,
		text : 'national library of medicine',
		to : '/home/tags/national library of medicine'
	}
]

function Recommendation() {



  return (
    <RecommendationContainer>
      <RecommendationTrending>
				<div className="title">Trending Keywords</div>
				<div className="border"></div>
				<ul className="contents">
					{keywords.map(keyword => (
						<Link
							key= {keyword.id}
							to={keyword.to}
						>
							#{keyword.text}
						</Link>
					))}
				</ul>
			</RecommendationTrending>

    </RecommendationContainer>
  );
}

export default Recommendation;